export default class hoverVideo {
    constructor() {
        ($(window).width() > 1000) ? this.events() : null;
    }

    handlerVideoShow(e, $this) {
        const player = $('[data-player]');
        let video = $this.find('[data-video-player]');

        $('body').addClass('hover-video');
        $this.addClass('is-hover');

        video.appendTo(player);

        this.userAgentCheked();
    }

    handlerVideoHide($this) {
        let video = $('[data-player]').find('[data-video-player]');
        let media = $this.find('[data-media]');

        video.appendTo(media);

        $('body').removeClass('hover-video');
        $this.removeClass('is-hover');
    }

    userAgentCheked() {
        let video = document.querySelector('[data-video-play]');
        let isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

         // отключаю кастомные контролы для Safari
        if(isSafari) {
            $(container).addClass('controls-hide');
            video.controls = true;
        }
    }

    events() {
        const self = this;

        $(document).on('mouseenter', '[data-hover-video]', function(e) {
            self.handlerVideoShow(e, $(this));
        });
        $(document).on('mouseout', '[data-hover-video]', function() {
            self.handlerVideoHide($(this));
        });
    }
}