if(window.location.hash) window.setTimeout(() => { window.scrollTo(0, 0); }, 1);

function requireAll(r) {
    r.keys().forEach(r);
}

requireAll(require.context('./icons', true, /\.svg$/));

// Load plugins
import 'jquery'
import IMask from 'imask'

import svg4everybody from 'svg4everybody'
window.svg4everybody = svg4everybody;

import objectFitImages from 'object-fit-images'
window.objectFitImages = objectFitImages;

import imagesLoaded from 'imagesloaded'
window.imagesLoaded = imagesLoaded;

// import customScrollbar from 'malihu-custom-scrollbar-plugin'
// window.customScrollbar = customScrollbar;

// import slimScroll from 'jquery-slimscroll'
// window.slimScroll = slimScroll;

// import smoothScrollbar from 'smooth-scrollbar';
// window.smoothScrollbar = smoothScrollbar;

// load modules
import Utils from'./js/utils/utils'
import SvgUse from'./js/svgUse'
import Forms from'./js/forms/forms'
import MobileMenu from'./js/mobile-menu'
import Modals from'./js/modals'
import ScrollSection from'./js/scroll-section'
import Greet from'./js/greet'
import HoverImg from'./js/hover-img'
import HoverVideo from'./js/hover-video'
import Partners from'./js/partners'
import ElemIsInViewport from'./js/is-in-viewport'
import ElemIsInViewportBtn from'./js/is-in-viewport-btn'
import Nav from'./js/nav'
import LogoShort from'./js/logo-short'
import Contacts from'./js/contacts'

// Load styles
import './styles/app.js';

// Run components

window.App = {
    debug: false,
    lang: 'ru'
};

if (window.SITE_LANG) {
    App.lang = window.SITE_LANG;
}

if (App.debug) {
    console.log('Debug: ' + App.debug);
    console.log('Lang: ' + App.lang);
}

document.addEventListener('DOMContentLoaded', function() {
    objectFitImages();

    if('ontouchstart' in window || navigator.maxTouchPoints) $(document.body).addClass("touch");

    App.Utils = new Utils();
    App.Forms = new Forms();
    App.SvgUse = new SvgUse();
    App.MobileMenu = new MobileMenu();
    App.Modals = new Modals();
    App.ScrollSection = new ScrollSection();
    App.Greet = new Greet();
    App.HoverImg = new HoverImg();
    App.HoverVideo = new HoverVideo();
    App.Partners = new Partners();
    App.ElemIsInViewport = new ElemIsInViewport();
    App.ElemIsInViewportBtn = new ElemIsInViewportBtn();
    App.Nav = new Nav();
    App.LogoShort = new LogoShort();
    App.Contacts = new Contacts();

    $('[data-inputmask]').each(function () {
        IMask($(this)[0], {mask: "0 (000) 000-0000"});
    });


    // $('html body').mCustomScrollbar();

    window.fancy = $.fancybox;
});