    animateScroll(value) {
        const $page = $('html, body');

        console.log('value: ' + value);

        $page.animate({
            scrollTop: value
        }, 800);
        return false;
    }

    handlerMobile($this) {
        const $page = $('html, body');
        let $href = $this.attr('href'),
            indent = parseInt($('.content').css("padding-top"));

        this.handlerActive($this);
        App.MobileMenu.menuOpen();

        $page.animate({
            scrollTop: $($href).offset().top - indent
        }, 800);
        return false;
    }

    isInViewport($this) {
        let elmViewport = $this[0].getBoundingClientRect();
        let isViewport = $('.is-in-viewport')[0].getBoundingClientRect();

        return (
            (elmViewport.x < isViewport.x) && ((elmViewport.x + elmViewport.width) > isViewport.width)
        )
    };

    handlerScrollActive() {
        let self = this;
        let sectionTrigger = $('[data-trigger-btn-fixed]');
    }

    events() {
        const self = this;

        $(document).on('click', '.header-nav__link', function (e) {
            e.preventDefault();
            if ($(window).width() > 1000) {
                self.handler2($(this));
            } else {
                self.handlerMobile($(this));
            }
        });

        $(window).on('scroll load', () => {
            this.handlerScrollActive();
            // console.log('scrollTop: ' + $(window).scrollTop());
        });

    }

    init() {
        setTimeout(() => {
            $('[data-section]').each((index, section) => {
                ScrollTrigger.create({
                    trigger: `#${section.id}`,
                    start: "top top",
                    onEnter: () => this.handlerActive($(`[href="#${section.id}"]`)),
                    onEnterBack: () => this.handlerActive($(`[href="#${section.id}"]`))
                });
            })
        }, 2000)
    }
