export default class ElemIsInViewport {
    constructor() {
        ($(window).width() > 1000) ? this.events() : null;
    }

    isInViewport($this) {
        let elmViewport = $this[0].getBoundingClientRect();
        let isViewport = $('.is-in-viewport')[0].getBoundingClientRect();

        return (
            (elmViewport.x < isViewport.x) && ((elmViewport.x + elmViewport.width) > isViewport.width)
        )
    };

    animateScroll() {
        let self = this;
        let btnsTrigger = $('[data-trigger-btn-fixed]');

        if(btnsTrigger.length) {
            btnsTrigger.each(function() {
                let btns = $(this).siblings($('[data-btn-fixed-container]'));
                let btn = btns.find($('.btn'));

                if (self.isInViewport($(this))) {
                    btn
                        .addClass('is-move-fixed')
                        .appendTo($('body'));
                } else {
                    if ($('.btn.is-move-fixed').length) {
                        $('.btn.is-move-fixed')
                            .removeClass('is-move-fixed')
                            .appendTo(btns);
                    }
                }
            });
        }
        else $(window).off('scroll load', () => { this.animateScroll(); });
    }

    events() {
        $(window).on('scroll load', () => { this.animateScroll(); });
    }
}