export default class ElemIsInViewport {
    constructor() {
        ($(window).width() > 1000) ? this.events() : null;
    }

    isInViewport($this) {
        let elmViewport = $this[0].getBoundingClientRect();
        let isViewport = $('.is-in-viewport')[0].getBoundingClientRect();

        return (
            (elmViewport.top < (isViewport.top + isViewport.height) &&  (elmViewport.top + elmViewport.height) > (isViewport.top + isViewport.height))
        )
    };

    animateScroll() {
        const self = this;
        const view = $('[data-view-holder]');
        let viewImg = view.find('img');

        let items = $('[data-is-in-viewport]');

        if(items.length && $('body').hasClass('load')) {
            items.each(function() {
                let $this = $(this);
                let imgSrc = $this.attr('data-img-src');

                if (self.isInViewport($this)) {
                    $this.addClass('is-show');
                    view
                        .addClass('is-show')
                        .css({top: '50vh', left: 'auto'});
                    viewImg.attr('src', imgSrc);
                } else {
                    $this.removeClass('is-show');
                    // viewImg.attr('src', '');
                }

                if (items.hasClass('is-show')) {
                    $('body').addClass('hover-text');
                } else {
                    $('body').removeClass('hover-text');
                    view.removeClass('is-show');
                }
            });
        }
        else $(window).off('scroll load', () => { this.animateScroll(); });
    }

    events() {
        $(window).on('scroll load', () => { this.animateScroll(); });
    }
}