export default class LogoShort {
    constructor() {
        this.events();
    }

    isInViewportStart($this) {
        let elmViewport = $this[0].getBoundingClientRect();
        let isViewport = $('.is-in-viewport')[0].getBoundingClientRect();
        return (
            (elmViewport.x < (isViewport.x + isViewport.width))
        )
    };

    isInViewportEnd($this) {
        let elmViewport = $this[0].getBoundingClientRect();
        let isViewport = $('.is-in-viewport')[0].getBoundingClientRect();
        return (
            (elmViewport.x > (isViewport.x + isViewport.width))
        )
    };

    isInViewport($this) {
        let elmViewport = $this[0].getBoundingClientRect();
        let isViewport = $('.is-in-viewport')[0].getBoundingClientRect();
        return (
            (elmViewport.top < (isViewport.top + isViewport.height) &&  (elmViewport.top + elmViewport.height) > (isViewport.top + isViewport.height))
        )
    };

    animateScroll() {
        if (!$('#section04').length) return false

        const self = this;
        const logo = $('[data-logo-short]');
        let windowScroll = $(window).scrollTop();
        let windowHeight = $(window).height();
        let block = $('#section04');
        let blockTop = block.offset().top;
        let items = $('#section04 .horizon-text span').slice(1);
        let start = items.first();
        let end = items.last();

        if(items.length) {
            if ($(window).width() <= 1000) {
                if (blockTop < windowScroll && (blockTop + windowHeight / 2) > windowScroll) {
                    logo.addClass('is-show');
                } else {
                    logo.removeClass('is-show');
                }
            } else {
                items.addClass('add-after');

                if (self.isInViewportStart(start) && self.isInViewportEnd(end)) {
                    logo.addClass('is-show');
                } else {
                    logo.removeClass('is-show');
                }
            }
        }
        else $(window).off('scroll load', () => { this.animateScroll(); });
    }

    events() {
        $(window).on('scroll load', () => { this.animateScroll(); });
    }
}