export default class Partners {
    constructor() {
        ($(window).width() > 1000) ? this.events() : null;
    }

    isInViewport($this) {
        let elmViewport = $this[0].getBoundingClientRect();
        let isViewport = $('.is-in-viewport')[0].getBoundingClientRect();

        return (
            (elmViewport.top < (isViewport.top + isViewport.height) &&  (elmViewport.top + elmViewport.height + 50) > (isViewport.top + isViewport.height))
        )
    };

    animateScroll() {
        const self = this;
        const view = $('[data-view-partner-holder]');
        const viewContainer = view.find($('.partners__media'));
        let viewImgFirst = view.find('img').first();
        let viewImgLast = view.find('img').last();
        let items = $('[data-partners-trigger]');

        if(items.length && $('body').hasClass('load')) {
            items.each(function() {
                let $this = $(this);

                if (self.isInViewport($this)) {
                    let imgDouble = $this.attr('data-img-double');
                    let imgSrc = $this.attr('data-img-src').split(',');
                    let imgSrcFirst = imgSrc[0];
                    let imgSrcLast = imgSrc[1];

                    $this.addClass('is-show');
                    view.addClass('is-show');

                    if (imgDouble === "") {
                        view.addClass('is-double');
                        viewContainer.addClass('partners__media--double');
                        viewImgFirst.attr('src', imgSrcFirst);
                        viewImgLast.attr('src', imgSrcLast);
                    } else {
                        view.removeClass('is-double');
                        viewContainer.removeClass('partners__media--double');
                        viewImgFirst.attr('src', imgSrcFirst);
                    }
                } else {
                    $this.removeClass('is-show');
                    // viewImgFirst.attr('src', imgSrcFirst);
                    // viewImgLast.attr('src', imgSrcLast);
                }

                if (items.hasClass('is-show')) {
                    $('body').addClass('hover-partners');
                } else {
                    $('body').removeClass('hover-partners');
                    view.removeClass('is-show is-double');
                    viewImgFirst.attr('src', '');
                    viewImgLast.attr('src', '');
                }
            });
        }
        else $(window).off('scroll load', () => { this.animateScroll(); });
    }

    events() {
        $(window).on('scroll load', () => { this.animateScroll(); });
    }
}