export default class MobileMenu {
    menu = '[data-mobile-menu]';
    burger = '[data-burger]';

    constructor() {
        this.init();
        this.events();
    }

    init() {
        this.movingHandler();
        this.clone();
    }

    movingHandler() {
        if ($(window).width() > 1000) {
            $('[data-mobile="nav"]').find($('[data-move="nav"]')).appendTo('[data-desktop="nav"]');
        }
        else {
            $('[data-desktop="nav"]').find($('[data-move="nav"]')).appendTo('[data-mobile="nav"]');
        }
    }

    clone() {
        $('[data-desktop="socials"]').find($('.socials')).clone().appendTo('[data-mobile="socials"]');
    }

    menuOpen() {
        if (!$(this.menu).hasClass('is-open')) {
            $(this.menu).addClass('is-open');
            $('body').addClass('is-menu-open');
        } else {
            $(this.menu).removeClass('is-open');
            setTimeout(function () {
                $('body').removeClass('is-menu-open');
            }, 800);
        }
    }

    events() {
        const self = this;

        $(window).on('resize', function () {
            self.movingHandler();
        });

        $(document).on('click', this.burger, function(e) {
            e.preventDefault();
            self.menuOpen();
        })
    }
}