import {gsap} from 'gsap'
import {ScrollTrigger} from "gsap/ScrollTrigger";
import ASScroll from '@ashthornton/asscroll';


gsap.registerPlugin(ScrollTrigger);

export default class ScrollSection {
    constructor() {
        ($(window).width() > 1000) ? this.init() : null;
    }

    init() {

        setTimeout(function () {

            const asscroll = new ASScroll({
                customScrollbar: true,
                ease: 0.1
            });
            
            ScrollTrigger.defaults({
                scroller: ".inner"
            });
            
            ScrollTrigger.scrollerProxy(".inner", {
                scrollTop(value) {
                    return arguments.length
                        ? asscroll.scrollTo(value)
                        : -asscroll.smoothScrollPos;
                },
                getBoundingClientRect() {
                    return {
                        top: 0,
                        left: 0,
                        width: window.innerWidth,
                        height: window.innerHeight
                    };
                }
            });
            
            asscroll.on("raf", ScrollTrigger.update);
            ScrollTrigger.addEventListener("refresh", () => asscroll.onResize());

            const section01 = $('#section01');
            let section01HorizonTextWidth = section01.find($('.horizon-text')).outerWidth();
            let section01HorizonTextHeight= section01.find($('.horizon-text')).outerHeight();
            let startPin01 = section01.outerHeight() - section01HorizonTextHeight / 2;
            let endPin01 = startPin01 + section01HorizonTextWidth;

            gsap.to('#section01', {
                x: -section01HorizonTextWidth,
                scrollTrigger: {
                    trigger: '#sectionPin01',
                    start: () => 'top+=' + startPin01 + ' center',
                    end: () => 'top+=' + endPin01 + ' center',
                    pin: true,
                    anticipatePin: 1,
                    scrub: true,
                    // markers: true
                }
            });
            gsap.from('#section02', {
                x: section01HorizonTextWidth,
                scrollTrigger: {
                    trigger: '#sectionPin01',
                    start: () => 'top+=' + startPin01 + ' center',
                    end: () => 'top+=' + endPin01 + ' center',
                    pin: true,
                    anticipatePin: 1,
                    scrub: true,
                    // markers: true
                    onLeave: ()  => {

                    },
                    onEnterBack: () => {

                    }
                }
            });

            const section03 = $('#section03');
            let section03HorizonTextWidth = section03.find($('.horizon-text')).outerWidth();
            let startPin03 = section03.find('.section__content').outerHeight() + 300 + section01HorizonTextHeight / 2;
            let endPin03 = section03HorizonTextWidth;

            const section04 = $('#section04');
            let section04HorizonTextWidth = section04.find($('.horizon-text')).outerWidth();
            let startPin04 = section04.find('.section__content').outerHeight() - section01HorizonTextHeight / 2;
            let endPin04 = section04HorizonTextWidth;

            gsap.to('#section03', {
                x: -section03HorizonTextWidth,
                scrollTrigger: {
                    trigger: '#sectionPin02',
                    start: () => 'top+=' +  startPin03+ ' center',
                    end: () => 'top+=' + endPin03 + ' center',
                    pin: true,
                    anticipatePin: 1,
                    scrub: true,
                    // markers: true
                }
            });

            gsap.from('#section04', {
                x: section03HorizonTextWidth,
                scrollTrigger: {
                    trigger: '#sectionPin02',
                    start: () => 'top+=' + startPin03 + ' center',
                    end: () => 'top+=' + endPin03 + ' center',
                    pin: true,
                    anticipatePin: 1,
                    scrub: true,
                    // markers: true,
                    onLeave: ()  => {
                        $('#section04').prependTo('#sectionPin03');
                        let newHeight = parseInt($('#sectionPin02').closest('.pin-spacer').css('height')) - startPin04;
                        $('#sectionPin02').closest('.pin-spacer').css('height', newHeight);

                        section4();
                        // asscroll.on("raf", ScrollTrigger.update);
                    },
                    onEnterBack: () => {
                        $('#section04').appendTo('#sectionPin02');
                        let newHeight = parseInt($('#sectionPin02').closest('.pin-spacer').css('height')) + startPin04;
                        $('#sectionPin02').closest('.pin-spacer').css('height', newHeight);
                    }
                }
            });


            let section4 = () => {
                gsap.to('#section04', {
                    x: -section04HorizonTextWidth,
                    scrollTrigger: {
                        trigger: '#sectionPin03',
                        start: () => 'top+=' + startPin04 + ' center',
                        end: () => 'top+=' + endPin04 + ' center',
                        pin: true,
                        anticipatePin: 1,
                        scrub: true,
                        // markers: true
                    }
                });
                gsap.fromTo(
                    '#section05',
                    {x: section04HorizonTextWidth},
                    {x: 0 ,
                    scrollTrigger: {
                        trigger: '#sectionPin03',
                        start: () => 'top+=' + startPin04 + ' center',
                        end: () => 'top+=' + endPin04 + ' center',
                        pin: true,
                        anticipatePin: 1,
                        scrub: true,
                        // markers: true
                    }
                });


                const section07 = $('#section07');
                let startPin07 = section07.find('.section__content').outerHeight();

                const section08 = $('#section08');
                let section08HorizonTextWidth = section08.find($('.horizon-text')).outerWidth();
                let startPin08 = section08.find('.section__content').outerHeight() + startPin07 + 600 - section01HorizonTextHeight;
                let endPin08 = section08HorizonTextWidth;

                gsap.to('#section08', {
                    x: -section08HorizonTextWidth,
                    scrollTrigger: {
                        trigger: '#sectionPin04',
                        start: () => 'top+=' + startPin08 + ' center',
                        pin: true,
                        anticipatePin: 1,
                        scrub: true,
                        // markers: true
                    }
                });

                gsap.set('#section08', {
                    scrollTrigger: {
                        trigger: '#sectionPin04',
                        start: () => 'top+=' + startPin08 +  ' center',
                        toggleClass: 'is-color',
                        scrub: true,
                        // markers: true
                    }
                });
            };

            asscroll.enable(false, true, document.querySelector(".inner"));


        }, 2000);
    }
}