export default class Contacts {
    constructor() {
        ($(window).width() > 1000) ? this.init() : null;
    }

    init() {
        let container = $('#section08 .section__desc p').last();
        let containerText = container.text();
        let containerArr = containerText.split(' ');
        let phrase = [];
        let newContainer = $('#section08 .horizon-text');

        for (let i = 0; i < containerArr.length ; i++) {
            if (i < 2) {
                phrase.unshift(containerArr.pop());
            }
        }

        container.text(containerArr.join(' '));
        phrase = phrase.join(' ');

        newContainer.prepend(`<span>${phrase}</span>`);
    }
}