import { gsap } from 'gsap'
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export default class Greet {
    greet = '[data-greet]';
    flag = false;

    constructor() {
        this.init();
        this.events();
    }

    init() {
        gsap.fromTo('[data-greet]', {
                backgroundColor: '#323233',
            },
            {
            backgroundColor: '#ffffff00',
            ease: 'none',
            delay: 2
        });
        gsap.to('[data-greet-logo]', {
            duration: 2,
            opacity: 1,
            ease: 'none',
            delay: 1
        });
        gsap.to('[data-greet-logo-bg]', {
            duration: 2,
            x: 1500,
            ease: 'none',
            delay: 1
        });
        gsap.to('[data-greet-logo-color]', {
            duration: 1,
            x: 3500,
            ease: 'none',
            delay: 2.5
        });
        gsap.from('[data-logo-short]', {
            duration: 3,
            opacity: 0,
            scale: 1.4,
            ease: 'expo',
            delay: 3
        });
        gsap.to('[data-logo-short-bg]', {
            duration: 2,
            x: 1500,
            ease: 'none',
            delay: 3
        });
        gsap.fromTo('[data-greet-pic]', {
                y: 20
            },
            {
                duration: 2,
                opacity: 1,
                y: 0,
                ease: 'expo',
                delay: 5
            }
        );
        gsap.to('[data-page-scroll]', {
            duration: 2,
            opacity: 1,
            ease: 'none',
            delay: 5
        });
    }

    handlerScroll() {
        $('body').addClass('load');
        this.flag = true;
    }

    handlerGreetHide() {
        if (this.flag) {
            $(document).scrollTop(0);
            $(this.greet).addClass('is-hide');
            $('[data-page-scroll]').addClass('is-hide');
        }
    }

    handlerContentShow() {
        if (this.flag) {
            $('.flow-container').css('opacity', '1');

            gsap.from('#sectionPin01 .section__picture', {
                duration: 1,
                y: 30,
                opacity: 0,
                ease: 'expo',
                delay: 0
            });

            this.flag = false;
        }
    }

    handlerGreetMobile() {

        $(this.greet).addClass('is-hide');
        $('.flow-container').css('opacity', '1');
    }

    events() {
        const self = this;
        const pageScrollTrigger = document.getElementById('pageScroll');
        const greetSection = document.querySelector('.greet');
        const picture = document.querySelector('.greet__picture--left');

        if (pageScrollTrigger) {
            pageScrollTrigger.addEventListener("transitionend", function () {
                if ($(window).width() > 1000) {
                    self.handlerScroll();
                } else {
                    self.handlerGreetMobile();
                }
            });
        }

        if (greetSection && $(window).width() > 1000) {
            greetSection.addEventListener("transitionend", function () {
                self.handlerContentShow();
            });
        }

        if (picture && $(window).width() <= 1000) {
            picture.addEventListener("transitionend", function () {
                self.handlerGreetMobile();
            });
        }

        $(document).on('scroll', function () {
            self.handlerGreetHide();
        });
    }
}