export default class HoverImg {
    constructor() {
        ($(window).width() > 1000) ? this.events() : null;
    }

    handlerImgMove(e, $this) {
        const view = $('[data-view-holder]');
        let viewHeight = view.outerHeight(true) / 2;
        let viewWidth = view.outerWidth(true);
        let viewPicture = view.find('picture');
        let viewImg = view.find('img');
        let viewText = view.find('span');
        let imgSrc = $this.attr('data-img-src');
        let imgClass = $this.attr('data-img-class');
        let imgText = $this.find('.holder-hover__desc').text();
        let imgVertical = 0;
        let imgHorizontal = 0;

        if (view.hasClass('holder-hover--big')) {
            imgVertical = e.clientY;
            imgHorizontal = e.clientX;
        } else {
            imgVertical = e.clientY;
            imgHorizontal = e.clientX;
        }

        $('body').addClass('hover-image');
        $this.addClass('is-hover');

        view.addClass(`is-show ${imgClass}`)
            .css({top: imgVertical, left: imgHorizontal});

        viewImg.attr('src', imgSrc);
        viewText.text(imgText);
    }

    handlerImgHide($this) {
        const view = $('[data-view-holder]');
        let viewImg = view.find('img');
        let viewText = view.find('span');
        let imgClass = $this.attr('data-img-class');

        $('body').removeClass('hover-image');
        $this.removeClass('is-hover');
        view.attr('class', 'view-holder');
        // viewImg.attr('src', '');
        viewText.text('');
    }

    events() {
        const self = this;

        $(document).on('mousemove mouseenter mouseover', '[data-img-move]', function(e) {
            self.handlerImgMove(e, $(this));
        });
        $(document).on('mouseout', '[data-img-move]', function() {
            self.handlerImgHide($(this));
        });
    }
}